import React from "react";
import "./IPFIELD.scss";
export default props => {
  return (
    <div style={{ width: 250 }}>
      <div className="input-group">
        <input
          className="form-control comingFromLeft"
          value={props.value}
          type={props.type}
          name={props.name}
          onChange={e => {
            props.onChange(e);
          }}
        ></input>
        <label
          className={
            !!props.value ? "has-value comingFromLeft" : "comingFromLeft"
          }
        >
          {props.label}
        </label>
        <div className="line"></div>
      </div>
    </div>
  );
};
