import React from "react";
import SignUpHeader from "../../header/LoginHeader.js";

const Stage3 = props => {
  const keys = Object.keys(props.data);
  let List = keys.map(key => {
    return (
      <div key={key} style={{ display: "flex", justifyContent: "center" }}>
        <label
          style={{
            marginLeft: 10,
            marginRight: 10,
            fontWeight: "bolder"
          }}
        >
          {key}
          <i
            className="fas fa-long-arrow-alt-right"
            style={{ marginLeft: 10 }}
          ></i>
        </label>
        <h1>{props.data[key]}</h1>
      </div>
    );
  });
  return (
    <div>
      <SignUpHeader heading={"Verify Details"}></SignUpHeader>
      {props.hasSignUp ? (
        <div className="container is Fluid">
          <div className="notification is-primary comingFromLeft">
            <h1 style={{ fontWeight: "bolder" }}>
              You've Been SuccessFully Registered(Redirecting To LoginScreen in
              3 Seconds)
            </h1>
            <div
              className="spin"
              style={{ width: 50, height: 50, marginTop: 10 }}
            ></div>
          </div>
        </div>
      ) : (
        ""
      )}
      {props.internalServerError ? (
        <div className="container is Fluid">
          <div className="notification is-danger notificationSlide">
            <h1 style={{ fontWeight: "bolder" }}>
              Sorry !! We got Some Technical Difficulties
            </h1>
          </div>
        </div>
      ) : (
        ""
      )}
      <div
        className={"container is-fluid"}
        style={{
          display: "flex",
          justifyContent: "center"
          // flexDirection: "column"
        }}
      >
        <div
          className="notification comingFromDownP5"
          style={{ marginTop: 50 }}
        >
          {List}
        </div>
      </div>

      <div
        className={"container is-fluid"}
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "center"
        }}
      >
        <div
          className={"notification"}
          style={{
            background: "rgba(0,0,0,0.1)",
            marginLeft: 10,
            display: "flex",
            width: 500,
            justifyContent: "center",
            flexDirection: "column"
          }}
        >
          <button
            className={`button is-primary comingFromDown ${
              props.isLoading ? "is-loading" : ""
            }`}
            style={{ marginTop: 30 }}
            onClick={props.onSubmit}
          >
            SignUp
          </button>
          <button
            className={`button is-info comingFromDown`}
            style={{ marginTop: 10 }}
            onClick={() => {
              props.onBack();
            }}
          >
            <i className="fas fa-arrow-left arrowLeft100"></i>
          </button>
        </div>
      </div>
    </div>
  );
};

export default Stage3;
