import React from "react";
import "./App.css";
// import "bootstrap/dist/css/bootstrap.min.css";
import Adult from "./components/AuthAdu/AuthAdu";
import AHome from "./components/AuthenticatedHome/AHome";
import Registration from "./components/signup/registration";
import Login from "./components/login/login";
import { BrowserRouter, Route } from "react-router-dom";
import TS from "./components/testingSticky/testingSticky";
// import MH from "./components/sContent/sContent1";

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Route exact path="/signup" component={Registration}></Route>
        <Route exact path="/login" component={Login}></Route>
        <Route exact path="/home" component={AHome}></Route>
        <Route exact path="/" component={AHome}></Route>
        <Route exact path="/adult" component={Adult}></Route>
        <Route exact path="/stick" component={TS}></Route>
      </BrowserRouter>
    </div>
  );
}

export default App;
