import React, { Component } from "react";
// import axios from "axios";
import Stage1 from "./stages/stage1";
import Stage2 from "./stages/stage2";
import Stage3 from "./stages/stage3";
import moment from "moment";
import url from "../../strings/url";
import validator from "validator";
import BG from "../background/BG";

// const validator = require("validator");
export default class Registration extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      username: "",
      password: "",
      confirmpassword: "",
      dob: "",
      organization: "",
      phone: "",
      createdAt: moment(),
      calenderFocused: false,
      stage: 1,
      button: {
        stage1: {
          isLoading: false
        }
      },
      isPasswordMatch: true,
      isValidEmail: true,
      isPasswordLengthRight: true,
      isBack1: false,
      isBack2: false,
      isUserNameLengthRight: true,
      NotValidPhone: false,
      noOrg: false,
      isStage3: false,
      internalServerError: false,
      hasSignUp: false
    };

    this.onChangeHandler = this.onChangeHandler.bind(this);
    this.onSubmit1 = this.onSubmit1.bind(this);
    this.onSubmit2 = this.onSubmit2.bind(this);
    this.onSubmit3 = this.onSubmit3.bind(this);
    this.validate1 = this.validate1.bind(this);
    this.validate2 = this.validate2.bind(this);
    this.onBack1 = this.onBack1.bind(this);
    this.onBack2 = this.onBack2.bind(this);
    this.setOrganizationState = this.setOrganizationState.bind(this);
  }
  onBack2() {
    this.setState({ stage: 2 });
  }
  validate2() {
    if (this.state.username.length < 3) {
      this.setState({ isUserNameLengthRight: false });

      return false;
    }

    if (!validator.isMobilePhone(this.state.phone)) {
      this.setState({ NotValidPhone: true });

      return false;
    }
    if (this.state.organization === "") {
      this.setState({ noOrg: true });

      return false;
    }
    return true;
  }

  async onSubmit3(e) {
    this.setState({ isStage3: true });
    try {
      const headers = new Headers();
      headers.append("content-type", "application/json");
      const body = {
        email: this.state.email,
        password: this.state.password,
        organization: this.state.organization,
        username: this.state.username,
        phone: this.state.phone
      };
      const request = new Request(`${url}/user/signup`, {
        method: "POST",
        headers,
        body: JSON.stringify(body)
      });
      const res = await fetch(request);
      if (res.status === 500) {
        return this.setState({ internalServerError: true, isStage3: false });
      }
      this.setState({ hasSignUp: true });
      setTimeout(() => {
        this.props.history.push("/login");
      }, 3000);
    } catch (e) {
      return this.setState({ internalServerError: true, isStage3: false });
    }
  }
  onSubmit2(e) {
    const isValidated = this.validate2();

    if (isValidated) {
      this.setState({ stage: 3 });
    }
    e.preventDefault();
  }

  async validate1() {
    try {
      if (!validator.isEmail(this.state.email)) {
        return this.setState({
          isValidEmail: false,
          button: { stage1: { isLoading: false } }
        });
      }
      if (this.state.password.length < 8) {
        return this.setState({
          isPasswordLengthRight: false,
          button: { stage1: { isLoading: false } }
        });
      }
      if (!validator.isEmail(this.state.email)) {
        return this.setState({
          isValidEmail: false,
          button: { stage1: { isLoading: false } }
        });
      }

      const isPasswordMatch =
        this.state.password === this.state.confirmpassword;
      if (!isPasswordMatch) {
        return this.setState({
          isPasswordMatch: false,
          button: { stage1: { isLoading: false } }
        });
      }
      const headers = new Headers();
      headers.append("content-type", "application/json");
      const request = new Request(
        `${url}/user/verifyEmail/${this.state.email}`,
        {
          method: "GET",
          headers
        }
      );
      const response = await fetch(request);
      const { isEmailValid } = await response.json();

      if (isEmailValid && isPasswordMatch) {
        return this.setState({
          stage: 2,
          button: { stage1: { isLoading: false } },
          isPasswordLengthRight: true,
          isPasswordMatch: true,
          isValidEmail: true
        });
      }
      this.setState({
        isValidEmail: false,
        button: { stage1: { isLoading: false } }
      });
    } catch (e) {
      this.setState({
        isValidEmail: false,
        button: { stage1: { isLoading: false } }
      });
    }
  }
  onChangeHandler(event) {
    this.setState({
      [event.target.name]: event.target.value,
      internalServerError: false
    });

    if (event.target.name === "email") {
      this.setState({ isValidEmail: true });
    }
    if (
      event.target.name === "password" ||
      event.target.name === "confirmpassword"
    ) {
      this.setState({ isPasswordMatch: true, isPasswordLengthRight: true });
    }

    if (event.target.name === "phone") {
      this.setState({ NotValidPhone: false });
    }

    if (event.target.name === "username") {
      this.setState({ isUserNameLengthRight: true });
    }
  }

  onBlurChangeHandler(event) {
    if (event.name === "passsword") {
    }
  }
  onSubmit1(e) {
    e.preventDefault();
    this.setState({ button: { stage1: { isLoading: true } } });
    this.validate1();
  }
  onBack1(e) {
    this.setState({ stage: 1, isBack1: true });
  }
  setOrganizationState({ organization, isCollegeSelected }) {
    this.setState({ organization, isCollegeSelected });
  }
  render() {
    return (
      <div>
        <BG transparency={0.6}></BG>
        {this.state.stage === 1 ? (
          <Stage1
            onChangeHandler={this.onChangeHandler}
            onSubmit={this.onSubmit1}
            isBack={this.state.isBack1}
            history={this.props.history}
            isLoading={this.state.button.stage1.isLoading}
            isValidEmail={this.state.isValidEmail}
            isPasswordMatch={this.state.isPasswordMatch}
            isPasswordLengthRight={this.state.isPasswordLengthRight}
            email={this.state.email}
            password={this.state.password}
            confirmpassword={this.state.confirmpassword}
          />
        ) : null}
        {this.state.stage === 2 ? (
          <Stage2
            onChangeHandler={this.onChangeHandler}
            username={this.state.username}
            phone={this.state.phone}
            isBack={this.state.isBack2}
            onBack={this.onBack1}
            isSelected={this.state.isCollegeSelected}
            setState={this.setOrganizationState}
            onSubmit={this.onSubmit2}
            isUsernameCorrect={this.state.isUserNameLengthRight}
            NotValidPhone={this.state.NotValidPhone}
            noOrg={this.state.noOrg}
          />
        ) : null}
        {this.state.stage === 3 ? (
          <Stage3
            data={{
              Username: this.state.username,
              Email: this.state.email,
              Phone: this.state.phone,
              College: this.state.organization
            }}
            onBack={this.onBack2}
            onSubmit={this.onSubmit3}
            isLoading={this.state.isStage3}
            hasSignUp={this.state.hasSignUp}
            internalServerError={this.state.internalServerError}
          ></Stage3>
        ) : null}
      </div>
    );
  }
}
