import React from "react";

export default function(props) {
  return (
    <div>
      <div
        style={{
          display: "block",
          position: "absolute",
          left: 0,
          right: 0,
          width: "100%",
          zIndex: -2,
          height: "110%",
          backgroundImage: `url(${
            !!props.image
              ? props.image
              : "https://i.pinimg.com/originals/3b/8a/d2/3b8ad2c7b1be2caf24321c852103598a.jpg"
          })`,
          backgroundSize: "auto"
        }}
      ></div>
      <div
        style={{
          display: "block",
          position: "absolute",
          left: 0,
          right: 0,
          width: "100%",
          zIndex: -1,
          height: "110%",
          background: `rgba(0,0,0,${props.transparency})`
        }}
      ></div>
    </div>
  );
}
