import React from "react";
import SignUpHeader from "../../header/LoginHeader.js";

const Stage1 = props => {
  return (
    <div>
      <div style={{ display: "flex", justifyContent: "center" }}>
        <SignUpHeader classname={"comingFromRight"}></SignUpHeader>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          marginBottom: 100
        }}
      >
        <form onSubmit={props.onSubmit}>
          <div
            className={`notification ${
              props.isBack ? "comingFromLeft" : "comingFromUpP5"
            }`}
            style={{ background: "transparent" }}
          >
            <div
              className={`box ${
                props.isBack ? "comingFromLeft" : "comingFromRightP5"
              }`}
              style={{ marginLeft: 10 }}
            >
              <div className={"input-field comingFromLeft"}>
                <input
                  className={`${
                    !props.isValidEmail ? "is-danger-field" : null
                  }`}
                  type="text"
                  name="email"
                  // placeholder="email"
                  value={props.email}
                  onChange={props.onChangeHandler}
                  required
                ></input>
                <label>Email</label>

                {!props.isValidEmail ? (
                  <p className="help is-danger comingFromLeftP5">
                    This email is Already Taken
                  </p>
                ) : null}
              </div>
            </div>

            <div
              className={`box ${
                props.isBack ? "comingFromLeft" : "comingFromLeftP5"
              }`}
              style={{ marginLeft: 10 }}
            >
              <div className={"input-field comingFromRight"}>
                <input
                  className={`${
                    !props.isPasswordMatch || !props.isPasswordLengthRight
                      ? "is-danger-field"
                      : null
                  }`}
                  type="password"
                  name="password"
                  // placeholder="Password"
                  value={props.password}
                  onChange={props.onChangeHandler}
                  required
                ></input>
                <label>Password</label>
                {!props.isPasswordLengthRight ? (
                  <p className="help is-danger comingFromLeftP5">
                    {"Password Length Must Be Greater Than 7"}
                  </p>
                ) : null}
              </div>
            </div>

            <div
              className={`box ${
                props.isBack ? "comingFromLeft" : "comingFromRightP5"
              }`}
              style={{ marginLeft: 10 }}
            >
              <div className={"input-field comingFromLeft"}>
                <input
                  className={`${
                    !props.isPasswordMatch ? "is-danger-field" : null
                  }`}
                  type="password"
                  name="confirmpassword"
                  value={props.confirmpassword}
                  onChange={props.onChangeHandler}
                  required
                ></input>

                <label>Password ( Re-Enter )</label>
                {!props.isPasswordMatch ? (
                  <p className="help is-danger comingFromLeftP5">
                    {"Password doesn't Match"}
                  </p>
                ) : null}
              </div>
            </div>

            <div
              className={"notification"}
              style={{
                background: "transparent",
                marginLeft: 10,
                display: "flex",
                justifyContent: "center",
                flexDirection: "column"
              }}
            >
              <button
                className={`button is-primary ${
                  props.isBack ? "comingFromLeft" : "comingFromLeftP5"
                } ${props.isLoading ? "is-loading" : null}`}
                style={{ marginLeft: 10 }}
                type="submit"
              >
                Next
              </button>
              <h1 style={{ marginTop: 10, fontWeight: "bolder" }}>
                ------- OR -------
              </h1>
              <button
                type="button"
                className={`button is-info ${
                  props.isBack ? "comingFromLeft" : "comingFromRight"
                }`}
                style={{ marginTop: 10, marginLeft: 10 }}
                onClick={event => props.history.push("/login")}
              >
                Already have Account?
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Stage1;
