import React, { Component } from "react";
import LH from "../../header/LoginHeader.js";

export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = { content: false };

    this.onClick = this.onClick.bind(this);
  }
  onClick(e) {
    this.setState(state => ({
      content: !state.content
    }));
  }
  render() {
    return (
      <div>
        <LH heading="Home"></LH>
        <button className="button is-danger comingFromDown">Log Out</button>
        <h1 style={{ fontWeight: "bold" }}>Or</h1>
        <div
          className="comingFromRight"
          style={{
            display: "flex",
            justifyContent: "center",
            marginBottom: 10,
            marginTop: 10
          }}
        >
          <a href="/adult" style={{ color: "grey" }}>
            <h1
              style={{
                fontFamily: "poppins",
                fontWeight: "bold",
                border: "3px solid",
                width: 350,
                borderRadius: 10
              }}
            >
              Click Here To Have A journey To Heaven...
            </h1>
          </a>
        </div>
      </div>
    );
  }
}

