import React from "react";

export default function(props) {
  return (
    <div className={"notification comingFromUp"} style={{ marginTop: 10 }}>
      <div className={`input-field comingFromRight`}>
        <input
          className={props.valid ? "" : "is-danger-field"}
          type="password"
          name="enteredPass"
          placeholder="Enter Password To Unlock Heaven"
          onChange={props.onChange}
          value={props.password}
          style={{ padding: 0, width: 300 }}
        ></input>
      </div>
      <button
        className={`button is-primary ${props.isLoading ? "is-loading" : null}`}
        style={{
          border: "none",
          marginTop: 10,

          boxShadow: "0px 30px 30px grey"
        }}
        onClick={props.onClick}
      >
        Careful !! Heaven Inside...
      </button>
    </div>
  );
}
